"use client"

import { useEffect } from "react"
import * as THREE from "three"

interface GameMenuProps {
  onSelectGame: (game: "next" | "missing" | "sort") => void
}

export default function GameMenu({ onSelectGame }: GameMenuProps) {
  useEffect(() => {
    const container = document.getElementById("menu-canvas-container")
    if (!container) return

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000)
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    renderer.setSize(window.innerWidth, window.innerHeight)
    renderer.setClearColor(0x000000, 0)
    renderer.outputColorSpace = THREE.SRGBColorSpace

    container.appendChild(renderer.domElement)
    camera.position.z = 50

    const games = [
      { title: "T√¨m S·ªë Ti·∫øp Theo", color: 0x3b82f6, posX: -25, hue: 0.6 },
      { title: "S·ªë C√≤n Thi·∫øu", color: 0xf97316, posX: 0, hue: 0.1 },
      { title: "S·∫Øp X·∫øp D√£y S·ªë", color: 0xa855f7, posX: 25, hue: 0.8 },
    ]

    const spheres: THREE.Mesh[] = []

    games.forEach((game, idx) => {
      // Main sphere
      const geometry = new THREE.IcosahedronGeometry(2.5, 4)
      const material = new THREE.MeshStandardMaterial({
        color: game.color,
        metalness: 0.4,
        roughness: 0.3,
        emissive: game.color,
        emissiveIntensity: 0.3,
      })
      const sphere = new THREE.Mesh(geometry, material)
      sphere.position.x = game.posX
      sphere.position.y = 0
      sphere.userData = { originalY: 0, speed: 0.001, index: idx }
      scene.add(sphere)
      spheres.push(sphere)

      // Orbiting particles around each sphere
      const particleCount = 8
      for (let i = 0; i < particleCount; i++) {
        const pGeometry = new THREE.SphereGeometry(0.3, 16, 16)
        const pMaterial = new THREE.MeshStandardMaterial({
          color: game.color,
          metalness: 0.6,
          roughness: 0.2,
          emissive: game.color,
          emissiveIntensity: 0.5,
        })
        const particle = new THREE.Mesh(pGeometry, pMaterial)
        const angle = (i / particleCount) * Math.PI * 2
        particle.userData = {
          parentIndex: idx,
          angle: angle,
          distance: 5,
          speed: 0.02,
        }
        scene.add(particle)
      }
    })

    // Enhanced lighting
    const light1 = new THREE.PointLight(0x00d9ff, 2, 100)
    light1.position.set(40, 40, 40)
    scene.add(light1)

    const light2 = new THREE.PointLight(0xff006e, 2, 100)
    light2.position.set(-40, -40, 40)
    scene.add(light2)

    const light3 = new THREE.PointLight(0xfdb813, 1.5, 80)
    light3.position.set(0, 50, 0)
    scene.add(light3)

    const ambientLight = new THREE.AmbientLight(0xffffff, 0.6)
    scene.add(ambientLight)

    const animate = () => {
      requestAnimationFrame(animate)

      const time = Date.now() * 0.001

      // Animate main spheres
      spheres.forEach((sphere) => {
        sphere.rotation.x += 0.002
        sphere.rotation.y += 0.004
        sphere.rotation.z += 0.001

        sphere.position.y = sphere.userData.originalY + Math.sin(time * 0.8 + sphere.userData.index) * 1.5

        // Pulsing scale
        const scale = 1 + Math.sin(time * 1.2 + sphere.userData.index * 1.5) * 0.15
        sphere.scale.set(scale, scale, scale)
      })

      // Animate orbiting particles
      scene.children.forEach((child) => {
        if (child instanceof THREE.Mesh && child.userData.parentIndex !== undefined) {
          const parentSphere = spheres[child.userData.parentIndex]
          child.userData.angle += child.userData.speed
          child.position.x = parentSphere.position.x + Math.cos(child.userData.angle) * child.userData.distance
          child.position.y = parentSphere.position.y + Math.sin(child.userData.angle * 0.5) * child.userData.distance
          child.position.z = Math.sin(child.userData.angle * 1.2) * child.userData.distance * 0.5

          child.rotation.x += 0.03
          child.rotation.y += 0.03

          // Color pulsing
          const pulse = Math.sin(time * 2 + child.userData.parentIndex) * 0.5 + 0.5
          child.material.emissiveIntensity = pulse * 0.8 + 0.2
        }
      })

      renderer.render(scene, camera)
    }

    animate()

    const handleResize = () => {
      camera.aspect = window.innerWidth / window.innerHeight
      camera.updateProjectionMatrix()
      renderer.setSize(window.innerWidth, window.innerHeight)
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
      renderer.dispose()
      container.removeChild(renderer.domElement)
    }
  }, [])

  return (
    <div className="relative w-full min-h-screen flex flex-col items-center justify-center overflow-hidden">
      <div id="menu-canvas-container" className="absolute inset-0 -z-10" />

      <div className="absolute inset-0 bg-gradient-to-b from-slate-950 via-blue-950 to-slate-950 opacity-40 -z-10" />

      <main className="relative z-10 w-full h-full flex flex-col items-center justify-center p-6">
        <div className="text-center mb-16 slide-down max-w-4xl">
          <h1 className="text-6xl md:text-8xl font-black mb-4 leading-tight bg-gradient-to-r from-cyan-400 via-blue-500 to-purple-600 bg-clip-text text-transparent drop-shadow-lg">
            H·ªá M·∫∑t Tr·ªùi
          </h1>
          <h2 className="text-3xl md:text-5xl font-bold mb-3 text-blue-300">Tr√≤ Ch∆°i To√°n H·ªçc</h2>
          <p className="text-lg md:text-xl text-slate-300 font-medium">Kh√°m Ph√° Quy Lu·∫≠t D√£y S·ªë C√πng C√°c H√†nh Tinh</p>
        </div>

        {/* Game Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8 md:gap-10 max-w-7xl w-full">
          {/* Game 1 */}
          <button
            onClick={() => onSelectGame("next")}
            className="group relative h-96 cursor-pointer transition-all duration-500 hover:scale-110 active:scale-95 perspective"
          >
            <div className="absolute inset-0 bg-gradient-to-br from-blue-600 via-blue-500 to-cyan-400 rounded-3xl shadow-2xl group-hover:shadow-3xl transition-all group-hover:-translate-y-2 group-hover:scale-105" />
            <div className="absolute inset-0 bg-white/10 rounded-3xl backdrop-blur-lg group-hover:backdrop-blur-xl transition-all" />

            <div className="relative h-full flex flex-col items-center justify-center p-8 rounded-3xl">
              <div className="text-7xl mb-6 animate-bounce-gentle">üî¢</div>
              <h3 className="text-2xl md:text-3xl font-black text-white mb-3 text-balance drop-shadow-lg">
                T√¨m S·ªë Ti·∫øp Theo
              </h3>
              <p className="text-sm md:text-base text-blue-100 text-center mb-6 leading-relaxed">
                T√¨m quy lu·∫≠t v√† ch·ªçn s·ªë ti·∫øp theo trong d√£y s·ªë
              </p>
              <div className="px-5 py-2 bg-white/25 rounded-full text-xs font-bold text-white backdrop-blur-md border border-white/30">
                ‚≠ê D·ªÖ - Trung B√¨nh
              </div>
            </div>
          </button>

          {/* Game 2 */}
          <button
            onClick={() => onSelectGame("missing")}
            className="group relative h-96 cursor-pointer transition-all duration-500 hover:scale-110 active:scale-95 perspective"
          >
            <div className="absolute inset-0 bg-gradient-to-br from-orange-500 via-rose-500 to-pink-500 rounded-3xl shadow-2xl group-hover:shadow-3xl transition-all group-hover:-translate-y-2 group-hover:scale-105" />
            <div className="absolute inset-0 bg-white/10 rounded-3xl backdrop-blur-lg group-hover:backdrop-blur-xl transition-all" />

            <div className="relative h-full flex flex-col items-center justify-center p-8 rounded-3xl">
              <div className="text-7xl mb-6 animate-bounce-gentle" style={{ animationDelay: "0.1s" }}>
                ‚ùì
              </div>
              <h3 className="text-2xl md:text-3xl font-black text-white mb-3 text-balance drop-shadow-lg">
                S·ªë C√≤n Thi·∫øu
              </h3>
              <p className="text-sm md:text-base text-orange-100 text-center mb-6 leading-relaxed">
                T√¨m s·ªë b·ªã thi·∫øu trong d√£y s·ªë ƒë·∫ßu ti√™n
              </p>
              <div className="px-5 py-2 bg-white/25 rounded-full text-xs font-bold text-white backdrop-blur-md border border-white/30">
                ‚≠ê‚≠ê Trung B√¨nh
              </div>
            </div>
          </button>

          {/* Game 3 */}
          <button
            onClick={() => onSelectGame("sort")}
            className="group relative h-96 cursor-pointer transition-all duration-500 hover:scale-110 active:scale-95 perspective"
          >
            <div className="absolute inset-0 bg-gradient-to-br from-purple-600 via-violet-500 to-pink-500 rounded-3xl shadow-2xl group-hover:shadow-3xl transition-all group-hover:-translate-y-2 group-hover:scale-105" />
            <div className="absolute inset-0 bg-white/10 rounded-3xl backdrop-blur-lg group-hover:backdrop-blur-xl transition-all" />

            <div className="relative h-full flex flex-col items-center justify-center p-8 rounded-3xl">
              <div className="text-7xl mb-6 animate-bounce-gentle" style={{ animationDelay: "0.2s" }}>
                üìä
              </div>
              <h3 className="text-2xl md:text-3xl font-black text-white mb-3 text-balance drop-shadow-lg">
                S·∫Øp X·∫øp D√£y S·ªë
              </h3>
              <p className="text-sm md:text-base text-purple-100 text-center mb-6 leading-relaxed">
                S·∫Øp x·∫øp c√°c s·ªë theo quy lu·∫≠t tƒÉng d·∫ßn
              </p>
              <div className="px-5 py-2 bg-white/25 rounded-full text-xs font-bold text-white backdrop-blur-md border border-white/30">
                ‚≠ê‚≠ê‚≠ê Kh√≥
              </div>
            </div>
          </button>
        </div>

        {/* Call to action */}
        <div className="mt-20 text-center text-slate-300 font-medium slide-down" style={{ animationDelay: "0.3s" }}>
          <p className="text-lg">Ch·ªçn m·ªôt tr√≤ ch∆°i ƒë·ªÉ b·∫Øt ƒë·∫ßu h·ªçc to√°n vui v·∫ª! üöÄ</p>
        </div>
      </main>
    </div>
  )
}
