"use client"

import { useState } from "react"
import GameMenu from "@/components/game-menu"
import NextNumberGame from "@/components/games/next-number-game"
import MissingNumberGame from "@/components/games/missing-number-game"
import SortSequenceGame from "@/components/games/sort-sequence-game"
import SolarSystemBackground from "@/components/solar-system-background"

export default function Home() {
  const [currentGame, setCurrentGame] = useState<null | "next" | "missing" | "sort">("menu")

  const renderContent = () => {
    switch (currentGame) {
      case "next":
        return <NextNumberGame onBack={() => setCurrentGame("menu")} />
      case "missing":
        return <MissingNumberGame onBack={() => setCurrentGame("menu")} />
      case "sort":
        return <SortSequenceGame onBack={() => setCurrentGame("menu")} />
      case "menu":
      default:
        return <GameMenu onSelectGame={(game) => setCurrentGame(game)} />
    }
  }

  return (
    <>
      <SolarSystemBackground />
      <main className="w-full h-screen flex items-center justify-center overflow-hidden relative z-10">
        {renderContent()}
      </main>
    </>
  )
}
